import { useRef, useState } from 'react'
import request from '../../services/https/index'
import './Login.scss'
import imgLogo from '../../assets/images/kiwi-login.svg'

const LoginPage = ({ setToken }) => {
    const EmailInputRef = useRef(null)
    const PassworddInputRef = useRef(null)
    const [hasError, setHesError] = useState(false);
    const HandleSubmit = (e) => {
        e.preventDefault();
        request.post('/login', {
            email: EmailInputRef.current.value,
            password: PassworddInputRef.current.value
        })
            .then((response) => {
                window.localStorage.setItem('sessionToken', response.data.token);
                setToken(response.data.token)
            })
            .catch((err) => {
                setHesError(true)
            })
    }
    // email: eve.holt@reqres.in;
    // password: cityslicka
    return (
        <>
            <div className="wrapper">
                <div className="container">
                    <div className="left">
                        <h2>Ваша регистрация завершена!</h2>
                        <p className='info'>Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер</p>
                        <form className="contact" onSubmit={HandleSubmit} >
                            <label htmlFor="email">
                                Введите email или номер телефона
                            </label>
                            <input ref={EmailInputRef} type="email" name="" id="email" placeholder="99891 167 27 23" />
                            <label htmlFor="password">
                                Пароль
                            </label>
                            <input ref={PassworddInputRef} type="password" name="" id="password" placeholder='***********' />
                            <button type="submit">Войти</button>
                        </form>
                        <div className="bottom">
                            <span>или</span>
                        </div>
                        <a href="#">Восстоновить ваше пароль</a>
                    </div>
                    <div className="right">
                        <div className="extra">
                            <img src={imgLogo} alt=""/>
                            <p>Откройте для себя 
                            целую плошадку
                            для продаже</p>
                            <a href="#">Как это делать?</a>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default LoginPage